import Cookies from 'js-cookie';
import axios from 'axios';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL
} from './types'

// it matters what order these variables are.
export const register = (username, password, email, confirmation) => async dispatch => {
    const config = {
        credentials: 'include',
        mode: 'same-origin',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }


    const body = JSON.stringify({ username, password, email, confirmation })

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/network/register`, body, config)
        if (res.data.error) {
            console.log(res.data.error)
            dispatch({
                type: REGISTER_FAIL
            })
        } else {
            dispatch({
                type: REGISTER_SUCCESS
            })
        }

    } catch (err) {
        dispatch({
            type: REGISTER_FAIL
        })

    }
}

// redux thunk lets us do dispatch
export const login = (username, password) => async dispatch => {
    const config = {
        credentials: 'include',
        mode: 'same-origin',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }
    const body = JSON.stringify({ username, password })

    try {
        const res = await axios.post(`http://localhost:8000/network/login`, body, config)

        if (res.data.error) {
            console.log(res.data.error)
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data.username
            })
            // load user
        } else {
            dispatch({
                type: LOGIN_FAIL
            })
        }

    } catch(err) {
        dispatch({
            type: LOGIN_FAIL
        })

    }

}

export const logout = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    }

    const body = JSON.stringify({
        'withCredentials': true
    });

    try {
        const res = await axios.post(`http://localhost:8000/network/logout`, body, config)

        if (res.data.error) {
            console.log(res.data.error)
            dispatch({
                type: LOGOUT_SUCCESS,
            })
        } else {
            dispatch({
                type: LOGOUT_FAIL
            })
        }

    } catch(err) {
        dispatch({
            type: LOGOUT_FAIL
        })

    }

}